<template>
  <div>
    <LayoutBox one="活动详情">
      <div class="activity_box">
        <el-image class="activity_cover" :src="activityData.imageUrl" fit="cover" />
        <div>
          <h4>{{ activityData.activeName }}</h4>
          <div class="claim_group">
            <p>降价拍货品要求：</p>
            <p>1、标题：规格+品名+关键词；</p>
            <p>2、起拍价：起拍价低于woho主流常规销售价&低于拍卖日常销售价格；</p>
            <p>3、库存：≥3 4.其他具体要求详问各招商小二</p>
          </div>
          <div>
            <el-steps :active="1">
              <el-step title="开始报名：" :description="activityData.applyStartTime" />
              <el-step title="结束报名：" :description="activityData.applyEndTime" />
              <el-step title="活动开始：" :description="activityData.startTime" />
              <el-step title="活动结束：" :description="activityData.endTime" />
            </el-steps>
          </div>
          <div class="explanation">
            <p class="title">招商范围</p>
            <p class="content">此活动全网用户均可报名</p>
          </div>
          <div class="explanation">
            <p class="title">报名资质</p>
            <p class="content">您的店铺以达到要求</p>
          </div>
          <div class="explanation">
            <p class="title">活动规则</p>
            <p class="content">当前活动审核通过后不可撤销报名</p>
          </div>
          <div class="btn_group">
            <el-button type="danger" size="medium" round @click="onRegister">立即报名</el-button>
            <el-button v-if="!activityData.isCollecting" size="medium" style="width: 98px" round @click="onIsCollecting(1)">收藏</el-button>
            <el-button v-if="activityData.isCollecting" size="medium" style="width: 98px" round @click="onIsCollecting(0)">取消收藏</el-button>
          </div>
        </div>
      </div>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'PlatActivityData',

  components: {
    LayoutBox
  },

  data() {
    return {
      activityData: {
        imageUrl: ''
      } // 活动的信息
    }
  },

  created() {
    this.apiFindActivity(this.$route.query.id)
  },

  methods: {
    onRegister() {
      this.$router.push({ path: '/mark/plat/activity/platActivityData/activityGoods', query: this.activityData })
    },
    // 收藏或取消收藏
    onIsCollecting(status) {
      this.apiClickOrCanclickCollecting(status)
        .then((res) => {
          if (res) {
            switch (status) {
              case (0): {
                this.$message.success('取消收藏成功')
                this.activityData.isCollecting = 0
                break
              }
              case (1): {
                this.$message.success('收藏成功')
                this.activityData.isCollecting = 1
                break
              }
            }
          }
        })
    },

    // api接口 开始--------------------------------------
    // 根据id查活动
    apiFindActivity(id) {
      this.$http.post(`/woho/activity/manage/findById/${id}`)
        .then((res) => {
          this.activityData = res.data
        })
    },
    // 收藏或取消收藏
    apiClickOrCanclickCollecting(status) {
      const DATA = {
        activityId: this.$route.query.id,
        status: status
      }
      return this.$http.post(`/woho/activity/collecting/clickOrCanclickCollecting`, DATA)
        .then((res) => {
          return res.data.success
        })
    }
    // api接口 结束--------------------------------------
  }
}
</script>

<style lang="scss" scoped>
  .activity_box{
    display: flex;
    justify-content: space-around;
    .activity_cover{
      width: 256px;
      height: 299px;
    }
    .claim_group{
      font-size: 12px;
      color: #666666;
      margin: 30px 0px;
    }
    .explanation{
      margin-top: 30px;
      line-height: 7px;
      .title{
        font-size: 14px;
      }
      .content{
        font-size: 10px;
      }
    }
    .btn_group{
      margin-top: 60px;
    }
  }
</style>
